<template>
  <div class="bg-white p-2 rounded-lg">
    <div class="flex justify-center">
      <textarea
        class="p-2 mr-10 rounded-lg border-2 border-black bg-yellow-300"
        rows="5"
        v-model.lazy="markdownText"
      ></textarea>
      <div v-html="markdownToHtml"></div>
    </div>
  </div>
</template>
<script>
// http://localhost:8080/testmd
export default {
  data() {
    return {
      markdownText: "# hello _world_ \n abc**def**ghi",
    };
  },
  computed: {
    markdownToHtml() {
      return this.$store.markdown(this.markdownText);
    },
  },
};
</script>
